    submit = async () => {
        if (
            this.state.name != "" &&
            this.state.minprice != "" &&
            this.state.percent != "" &&
            this.state.expdate != "" &&
            this.state.times != ""
        ) {
            let data = null;
            let result = null;

            if (this.state.flat_discount.length > 5) {
                this.setState({
                    alert: true,
                    message: "Flat Discount must be maximum 5 digits",
                    type: "warning",
                });
                return;
            }
            if (this.state.per_discount.length > 3) {
                this.setState({
                    alert: true,
                    message: "% Discount must be maximum 3 digits",
                    type: "warning",
                });
                return;
            }

            if (this.state.edit_enable == false) {
                data = {
                    name: this.state.name,
                    minprice: this.state.minprice,
                    percent: this.state.percent,
                    expdate: this.state.expdate,
                    times: this.state.times,
                    discount_type: this.state.seller_discount,
                    discount_value:
                        this.state.seller_discount == "Flat discount"
                            ? this.state.flat_discount
                            : this.state.per_discount,
                };
                console.log(data);
                result = await HttpClient.requestData("/service-coupon", "POST", data);
            } else {
                data = {
                    name: this.state.name,
                    minprice: this.state.minprice,
                    percent: this.state.percent,
                    expdate: this.state.expdate,
                    times: this.state.times,
                    discount_type: this.state.seller_discount,
                    discount_value:
                        this.state.seller_discount == "Flat discount"
                            ? this.state.flat_discount
                            : this.state.per_discount,
                };
                console.log(data);
                result = await HttpClient.requestData(
                    "/service-coupon/" + this.state.edit_item_id,
                    "PUT",
                    data
                );
            }
            console.log("result", result);
            if (result && result.status) {
                this.setState({
                    alert: true,
                    message: this.state.edit_enable
                        ? "Coupon Updated Successfully"
                        : "Coupon Added Successfully",
                    type: "success",
                    name: "",
                    minprice: "",
                    percent: "",
                    expdate: "",
                    times: "",
                    discount_value: "",
                    discount_type: "",
                    edit_enable: false,
                    edit_item_id: "",
                });

                this.fetchData();
            } else {
                this.setState({
                    alert: true,
                    message: result.error,
                    type: "danger",
                });
            }
        } else {
            this.setState({
                alert: true,
                message: "Please Fill Up All Details.",
                type: "warning",
            });
        }
        setTimeout(() => {
            this.setState({
                alert: false,
                message: "",
                type: "",
            });
        }, 3000);
    };
