import React, { Component } from "react";
import {
  Card,
  CardBody,
  Col,
  Row,
  Container,
  FormGroup,
  Label,
  Input,
  CustomInput,
  Button,
  UncontrolledAlert,
  InputGroup,
} from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";
import { MDBDataTable, MDBDataTableV5 } from "mdbreact";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import HttpClient from "./../../utils/HttpClient";
import { Link } from "react-router-dom";
import { CSVLink } from "react-csv";
import Details from "./details";
import SellerShop from "./sellerShop"
class FormElements extends Component {
  constructor(props) {
    super(props);
    this.state = {
      breadcrumbItems: [
        { title: "Top Provider List", link: "#" },
        { title: "View Top Provider", link: "#" },
      ],
      customchk: true,
      toggleSwitch: true,
      // page states
      name: "",
      email: "",
      mobile: "",
      password: "",
      data: [],
      alert: false,
      message: "",
      type: "",
      result: [],
      edit_enable: false,
      edit_item_id: "",
      image_select: false,
      img_url: "",
      userId: "",
      userDetails: {},
      title: "",
      points: [""],
      id: "",
      duration: 0,
      seller_comission: "",
      desc: "",
      priority: "",
      device: "All",
      devicefilter: ["App", "Web", "All"],
    };

  }

  componentDidMount = async () => {

    this.fetchData();
  };





  fetchData = async (val) => {
    let data = {

      device_type: this.state.device == "All" ? "" : val

    };
    console.log("data", data);
    let result = await HttpClient.requestData("topProviders", "POST", data);
    console.log("seller List", result);
    if (result && result.status > 0) {
      let data = [];
      let i = 1;
      this.setState({ result: result.data });
      // console.log(this.state.pointarr);
      this.state.result.forEach((element, index) => {
        let rows = {
          sl: i,

          username: element.firstName + " " + element.lastName,
          email: element.email,
          date: moment(element.start).format("MMMM Do YYYY"),
          about: element.about,
          city: element.city,

          view: (
            <button
              title="Delete"
              className="btn btn-success mr-2"
              onClick={() => {
                this.setState({
                  activeid: element,
                });
                setTimeout(() => {
                  this.setState({
                    ismodalopen: true,
                    idneed: element._id
                  });
                }, 200);
              }}
            >
              View Details
            </button>
          ),
          seller_shop: (
            <Button
              className="btn-icon"
              color="primary"
              onClick={() => {
                this.setState({
                  activeid: element,
                });
                setTimeout(() => {
                  this.setState({
                    ismodalopen1: true,
                    sellerid: element._id
                  });
                }, 200);
              }}
            >
              {" "}
              <span className="btn-icon-label">
                {/* <i className="mdi mdi-emoticon-wink-outline mr-2"></i> */}
              </span>{" "}
              Seller Shop
            </Button>
          ),
          seller_service: (
            <Link
              to={{
                pathname:
                  "/seller-services/" + element._id,
                data: element, // your data array of objects
              }}
            >
              <Button
                className="btn-icon"
                color="primary"
              // onClick={(e) => this.status(index, element._id)}
              >
                {" "}
                <span className="btn-icon-label">
                  {/* <i className="mdi mdi-emoticon-wink-outline mr-2"></i> */}
                </span>{" "}
                Seller Services
              </Button>
            </Link>
          ),


        };
        i++;
        data.push(rows);
      });
      this.setState({
        data: data,
      });
    } else {
      this.setState({
        data: [],
      });
    }
  };

  onCloseCallBack = (val) => {
    console.log("object", val);
    this.setState({
      ismodalopen: val,
    });
  };


  onCloseCallBack1 = (val) => {
    console.log("object", val);
    this.setState({
      ismodalopen1: val,
    });
  };



  render() {
    const data = {
      columns: [
        {
          label: "Sl.",
          field: "sl",

          width: 150,
        },

        {
          label: "User Name",
          field: "username",

          width: 270,
        },
        {
          label: "Email",
          field: "email",

          width: 270,
        },
        {
          label: "About",
          field: "about",

          width: 270,
        },
        {
          label: "City",
          field: "city",

          width: 270,
        },
        {
          label: "View Details",
          field: "view",
          width: 100,
        },
        {
          label: "Seller Shop",
          field: "seller_shop",

          width: 170,
        },
        {
          label: "Seller Services",
          field: "seller_service",

          width: 170,
        },
        {
          label: "Device Type",
          field: "devtype",

          width: 270,
        },


      ],
      rows: this.state.data,
    };

    const header = [

      { label: "Sl.", key: "sl" },
      { label: "User Name", key: "username" },
      { label: "Email", key: "email" },
      { label: "About", key: "about" },
      { label: "City", key: "city" },
    ]
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid>
            <Breadcrumbs
              title={this.state.breadcrumbItems[0].title}
              breadcrumbItems={this.state.breadcrumbItems}
            />

            <Row>
              <Col xl={3}>
                {this.state.data.length > 0 ?
                  <CSVLink
                    data={this.state.data}
                    headers={header}
                    filename={"TopSeller.csv"}
                    className="btn btn-success">
                    Export Report in CSV
                  </CSVLink> : null}
              </Col>
            </Row>&nbsp;

            <Row className="mb-3">
              <Col md={3}>
                <select
                  className="form-control"
                  onChange={(val) => {
                    this.state.device = val.target.value;
                    this.setState({

                    })
                    this.fetchData(val.target.value);
                  }}
                  value={this.state.device}
                >
                  <option value="">Select Type</option>
                  {this.state.devicefilter.length > 0 &&
                    this.state.devicefilter.map((it, index) => {
                      return <option value={it}>{it}</option>;
                    })}
                </select>
              </Col>
            </Row>


            <Row>
              <Col xl={12}>
                <div className="card p-3">
                  {/* <MDBDataTable responsive bordered data={data} /> */}
                  <MDBDataTableV5
                    hover
                    entriesOptions={[5, 20, 25]}
                    entries={5}
                    pagesAmount={4}
                    data={data}
                  />
                </div>
                {this.state.ismodalopen ? (
                  <Details
                    status={true}
                    onCloseCallBack={this.onCloseCallBack}
                    data={this.state.activeid}
                    id={this.state.idneed}
                  />
                ) : null}
                {this.state.ismodalopen1 ? (
                  <SellerShop
                    status={true}
                    onCloseCallBack1={this.onCloseCallBack1}
                    sucess={this.sucess}
                    data={this.state.activeid}
                    sellerid={this.state.sellerid}
                  />
                ) : null}
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default FormElements;
